import React, { Component } from 'react';
import axios from 'axios';


class Form extends Component {
  constructor(props) {
	super(props);
	this.state = {
  	name: '',
  	email: '',
  	message: ''
	}
  }
  resetForm(){

     this.setState({name: '', email: '', budget: '', message: ''})
  }

  render() {
    require('./form.css');
    return (

      <form onSubmit={this.handleSubmit.bind(this)} method="POST">
      <input type="text" value={this.state.name} onChange={this.onNameChange.bind(this)} placeholder="Name" />
      <input type="email" value={this.state.email} onChange={this.onEmailChange.bind(this)} placeholder="Email" />
      <input type="text" value={this.state.budget} onChange={this.onBudgetChange.bind(this)} placeholder="Budget (optional)"/>
      <textarea value={this.state.message} onChange={this.onMessageChange.bind(this)} placeholder="Describe your project..."></textarea>
      <input id="submit" type="submit" value="Send Inquiry" />
      </form>

    );
  }

onNameChange(event) {
 this.setState({name: event.target.value})
 }

 onEmailChange(event) {
 this.setState({email: event.target.value})
 }

 onBudgetChange(event) {
 this.setState({budget: event.target.value})
 }
 onMessageChange(event) {
 this.setState({message: event.target.value})
 }

 handleSubmit(e){
     e.preventDefault();

     axios({
       method: "POST",
       url:"http://localhost:3002/send",
       data:  this.state
     }).then((response)=>{
       if (response.data.status === 'success'){
         alert("Message Sent.");
         this.resetForm()
       }else if(response.data.status === 'fail'){
         alert("Message failed to send.")
       }
     })
   }
 }
export default Form;



